<template>
	<a-modal
		v-model:visible="visibleModal"
		style="top: 50px"
		width="600px"
		title="Add Goods"
		:confirm-loading="confirmModalLoading"
		:afterClose="refreshModal"
		@ok="handleAddGoodsOk"
	>
		<a-form :label-col="{ flex: '100px' }" :wrapper-col="{ flex: 'auto' }">
			<a-form-item label="Goods">
				<InputSearchGoods
					v-model:goodsName="goods.goodsName"
					@selectGoods="handleSelectGoods"
					@searching="handleSearching"
				/>
			</a-form-item>
			<a-form-item label="Group">
				<a-select ref="select" v-model:value="goods.group" :disabled="goods.goodsID !== ''">
					<a-select-option v-for="(group, index) in UTILS_GROUP" :key="index" :value="group">
						{{ group }}
					</a-select-option>
				</a-select>
			</a-form-item>
			<a-form-item label="Quantity">
				<a-input v-model:value.number="goods.quantity" type="number"></a-input>
			</a-form-item>
			<a-form-item label="Expiry Date">
				<InputDate v-model:datetime="goods.expiryDate" />
			</a-form-item>
			<a-form-item label="Cost Price">
				<a-input v-model:value.number="goods.costPrice" addon-after=".000 vnđ" type="number"></a-input>
			</a-form-item>
			<a-form-item label="Retail Price">
				<a-input v-model:value.number="goods.retailPrice" addon-after=".000 vnđ" type="number"></a-input>
			</a-form-item>
			<a-form-item label="Wholesale Price">
				<a-input v-model:value.number="goods.wholesalePrice" addon-after=".000 vnđ" type="number"></a-input>
			</a-form-item>
		</a-form>
	</a-modal>
</template>

<script>
import { ref } from 'vue'
import { goodsList, addGoods, updateGoods } from '@/firebase/useWarehouse'
import { UTILS_GROUP } from '@/utils/constants'
import InputDate from '@/components/common/InputDate.vue'
import InputSearchGoods from '@/components/common/InputSearchGoods.vue'

export default {
	components: {
		InputDate,
		InputSearchGoods,
	},
	setup() {
		return {
			goods: ref({
				goodsID: '',
				goodsName: '',
				group: '',
				expiryDate: '',
				quantity: '',
				costPrice: '',
				retailPrice: '',
				wholesalePrice: '',
			}),
			oldSellPrice: ref({
				retailPrice: '',
				wholesalePrice: '',
			}),

			UTILS_GROUP,
			visibleModal: ref(false),
			confirmModalLoading: ref(false),
			goodsList,
		}
	},
	methods: {
		setDataModal(data) {
			this.goods.goodsID = data.goodsID || ''
			this.goods.goodsName = data.goodsName || ''
			this.goods.group = data.group || ''
			this.goods.expiryDate = data.expiryDate || '--'
			this.goods.quantity = data.quantity || 0
			this.goods.costPrice = data.costPrice || 0
			this.goods.retailPrice = data.retailPrice || 0
			this.goods.wholesalePrice = data.wholesalePrice || 0

			this.oldSellPrice.retailPrice = this.goods.retailPrice
			this.oldSellPrice.wholesalePrice = this.goods.wholesalePrice
		},
		openModal(data) {
			this.visibleModal = true
			if (data) this.setDataModal(data)
		},
		refreshModal() {
			this.setDataModal({})
			this.confirmModalLoading = false
		},
		handleSearching($event) {
			this.setDataModal({})
			this.goods.goodsName = $event
		},
		handleSelectGoods({ goodsID, expiryDate }) {
			const selectGoods = {
				goodsID,
				goodsName: this.goodsList[goodsID].goodsName,
				group: this.goodsList[goodsID].group,
				expiryDate: Number(expiryDate),
				quantity: this.goodsList[goodsID].stockAvail[expiryDate]?.quantity,
				costPrice: this.goodsList[goodsID].stockAvail[expiryDate]?.costPrice,
				retailPrice: this.goodsList[goodsID].retailPrice,
				wholesalePrice: this.goodsList[goodsID].wholesalePrice,
			}
			this.setDataModal(selectGoods)
		},
		notify(message) {
			this.$notification.open({
				message: 'Error !!!',
				description: message,
				placement: 'topRight',
				duration: 2,
			})
		},
		checkValidation() {
			if (!this.goods.quantity) {
				this.notify('Please enter quantity field !')
				return false
			}
			if (!this.goods.costPrice) {
				this.notify('Please enter cost price field !')
				return false
			}
			return true
		},
		async addUpdateGoods() {
			if (!this.goods.goodsID) {
				const newGoodsData = {
					goodsName: this.goods.goodsName,
					group: this.goods.group,
					retailPrice: this.goods.retailPrice,
					wholesalePrice: this.goods.wholesalePrice,
				}
				const newGoods = await addGoods(newGoodsData)
				this.goods.goodsID = newGoods.goodsID
			} else if (
				this.goods.retailPrice !== this.oldSellPrice.retailPrice ||
				this.goods.wholesalePrice !== this.oldSellPrice.wholesalePrice
			) {
				await updateGoods(this.goods.goodsID, {
					retailPrice: this.goods.retailPrice,
					wholesalePrice: this.goods.wholesalePrice,
				})
			}
		},
		async handleAddGoodsOk() {
			if (!this.checkValidation()) return

			this.confirmModalLoading = true
			await this.addUpdateGoods()
			const newStock = {
				goodsID: this.goods.goodsID,
				expiryDate: this.goods.expiryDate,
				costPrice: this.goods.costPrice,
				quantity: this.goods.quantity,
			}
			this.$emit('addStockIn', newStock)
			this.visibleModal = false
		},
	},
}
</script>

