<template>
	<a-auto-complete
		v-model:value="providerName"
		:filter-option="handleFilterProviderList"
		@search="handleSearching"
		@select="handleSelectProvider"
	>
		<template #dataSource>
			<a-select-option
				v-for="(provider, providerID) in providerList"
				:key="providerID"
				:value="provider.providerName"
			>
				{{ provider.providerName }}
			</a-select-option>
		</template>
		<a-input placeholder="Search Provider">
			<template #suffix>
				<a-tooltip v-if="providerID" title="Configuration">
					<CheckCircleTwoTone twoToneColor="blue" />
				</a-tooltip>
				<a-tooltip v-else title="Provider are not exists">
					<CloseCircleTwoTone twoToneColor="red" />
				</a-tooltip>
			</template>
		</a-input>
	</a-auto-complete>
</template>

<script>
import { ref } from 'vue'
import { CloseCircleTwoTone, CheckCircleTwoTone } from '@ant-design/icons-vue'
import { providerList } from '@/firebase/useProvider'
import { MySearch } from '@/utils/convert'

export default {
	components: { CloseCircleTwoTone, CheckCircleTwoTone },
	setup() {
		return {
			providerList,
			providerID: ref(''),
			providerName: ref(''),
		}
	},
	methods: {
		handleSearching(value) {
			this.providerID = ''
			this.$emit('searchingProvider', value)
		},
		handleSelectProvider(value, option) {
			this.providerID = option.key
			this.$emit('selectProvider', { id: option.key, data: providerList[option.key] })
		},
		handleFilterProviderList(input, option) {
			return MySearch(option.value, input)
		},
	},
}
</script>

<style></style>
