import { getFirestore, collection, getDoc, addDoc, query, onSnapshot } from 'firebase/firestore'
import { reactive } from 'vue'

const providerList = reactive({})
const db = getFirestore()
const qr = query(collection(db, 'PROVIDER'))

onSnapshot(qr, snapshot => {
	snapshot.docChanges().forEach(change => {
		if (change.type === 'added' || change.type === 'modified') {
			providerList[change.doc.id] = change.doc.data()
		} else if (change.type === 'removed') {
			delete providerList[change.doc.id]
		}
	})
})

const addProvider = async data => {
	const docRef = await addDoc(collection(db, 'PROVIDER'), {
		providerName: data.providerName,
		phone: data.phone,
		address: data.address,
		createdAt: new Date().getTime(),
		updatedAt: new Date().getTime(),
		removedAt: 0,
	})

	const docSnap = await getDoc(docRef)
	const providerID = docSnap.id
	const { providerName, phone, address } = docSnap.data()
	return {
		providerID,
		providerName,
		phone,
		address,
	}
}

export { providerList, addProvider }
