<template>
    <a-modal
        v-model:visible="visibleModal"
        width="900px"
        title="Create New Provider"
        :confirm-loading="confirmModalLoading"
        :afterClose="refreshModal"
        @ok="handleCreateProviderOk"
    >
        <a-form :model="newProvider" :label-col="{ span: 4 }" :wrapper-col="{ span: 20 }">
            <a-form-item label="Name">
                <a-input v-model:value="newProvider.providerName"></a-input>
            </a-form-item>
            <a-form-item label="Phone">
                <a-input v-model:value="newProvider.phone"></a-input>
            </a-form-item>
            <a-form-item label="Address">
                <a-input v-model:value="newProvider.address"></a-input>
            </a-form-item>
        </a-form>
    </a-modal>
</template>

<script>
import { ref, reactive } from 'vue'
import { addProvider } from '@/firebase/useProvider'

export default {
    setup() {
        return {
            visibleModal: ref(false),
            confirmModalLoading: ref(false),
            newProvider: reactive({ providerName: '', phone: '', address: '' }),
        }
    },
    methods: {
        openModal() {
            this.visibleModal = true
        },
        refreshModal() {
            this.newProvider.providerName = ''
            this.newProvider.phone = ''
            this.newProvider.address = ''
            this.confirmModalLoading = false
        },
        async handleCreateProviderOk() {
            this.confirmModalLoading = true
            const newProvider = await addProvider({
                providerName: this.newProvider.providerName,
                phone: this.newProvider.phone,
                address: this.newProvider.address,
            })
            this.$emit('newProviderCreated', newProvider)
            this.visibleModal = false
        },
    },
}
</script>

