<template>
	<a-form :label-col="{ flex: '100px' }" :wrapper-col="{ flex: 'auto' }" class="form">
		<a-form-item label="Provider">
			<InputSearchProvider
				ref="inputSearchProvider"
				@selectProvider="handleSelectProvider"
				@searchingProvider="handleSearchingProvider"
				style="width: calc(100% - 160px)"
			/>
			<a-button type="primary" @click="openModalCreateProvider">Create New Provider</a-button>
			<ModalCreateProvider ref="modalAddProvider" @newProviderCreated="handleCreateNewProvider" />
		</a-form-item>
		<a-form-item label="Shipping Cost">
			<a-input v-model:value.number="shippingCost" addon-after=".000 vnđ" type="number"></a-input>
		</a-form-item>
		<a-form-item label="Goods List">
			<div style="display:flex; justify-content: flex-end">
				<a-button @click="openModalAddStockIn">
					<template #icon>
						<PlusOutlined />
					</template>
					Add Goods
				</a-button>
			</div>
		</a-form-item>
		<ModalAddStockIn ref="modalAddStockIn" @addStockIn="addStockIn" />
		<div class="wrapper-table">
			<table class="table">
				<thead>
					<th>#</th>
					<th>Name</th>
					<th>Expiry Date</th>
					<th>Quantity</th>
					<th>Cost Price</th>
					<th>Total</th>
					<th>Action</th>
				</thead>
				<tbody>
					<tr v-if="Object.keys(stockIn).length === 0">
						<td colspan="7" style="text-align:center">No data available in table</td>
					</tr>
					<template v-for="(goods, goodsID, goodsIndex) in stockIn" :key="goodsIndex">
						<tr v-for="({ quantity, costPrice }, expiryDate, stockIndex) in goods" :key="stockIndex">
							<td style="text-align: center" v-if="stockIndex === 0" :rowspan="Object.keys(goods).length">
								{{ goodsIndex + 1 }}
							</td>
							<td v-if="stockIndex === 0" :rowspan="Object.keys(goods).length">
								{{ goodsList[goodsID].goodsName }}
							</td>
							<td style="text-align: right">{{ formatDateTime(Number(expiryDate)) }}</td>
							<td style="text-align: right">{{ quantity }}</td>
							<td style="text-align: right">{{ costPrice }}</td>
							<td style="text-align: right">{{ quantity * costPrice }}</td>
							<td style="text-align: center">
								<EditTwoTone
									twoToneColor="orange"
									class="delete-row-goods"
									@click="editStockIn(goodsID, expiryDate)"
								/>
							</td>
						</tr>
					</template>
					<tr style="text-align: right">
						<td colspan="5">Ship</td>
						<td>{{ shippingCost }}</td>
						<td></td>
					</tr>
					<tr style="text-align: right">
						<td colspan="5">Total Money</td>
						<td>{{ totalMoney }}</td>
						<td></td>
					</tr>
				</tbody>
			</table>
		</div>
		<a-form-item>
			<div style="display: flex; justify-content: center; margin-top: 1rem">
				<a-button type="primary" :loading="loadingCreateNote" @click="startCreateNote">
					Create
				</a-button>
				<a-button style="margin-left: 10px" @click="handleCancleForm">
					Cancel
				</a-button>
			</div>
		</a-form-item>
	</a-form>
</template>

<script>
import { ref, reactive, computed } from 'vue'
import { PlusOutlined, EditTwoTone } from '@ant-design/icons-vue'
import { addImportNote } from '@/firebase/useImportNote'
import { goodsList } from '@/firebase/useWarehouse'
import InputSearchProvider from '@/components/common/InputSearchProvider.vue'
import ModalCreateProvider from '@/components/common/ModalCreateProvider.vue'
import ModalAddStockIn from '@/components/import-note/import-note-add/ModalAddStockIn.vue'
import { MyFormatDateTime } from '@/utils/convert'

export default {
	components: {
		InputSearchProvider,
		ModalCreateProvider,
		ModalAddStockIn,
		PlusOutlined,
		EditTwoTone,
	},
	setup() {
		const shippingCost = ref('')
		const stockIn = reactive({})
		return {
			goodsList,
			providerID: ref(''),
			loadingCreateNote: ref(false),
			shippingCost,
			stockIn,
		}
	},
	computed: {
		totalMoney() {
			const tottalPrice = Object.values(this.stockIn).reduce((accGoods, goods) => {
				const eachGoods = Object.values(goods).reduce((accDate, date) => {
					const eachDate = date.quantity * date.costPrice
					return accDate + eachDate
				}, 0)
				return accGoods + eachGoods
			}, 0)
			return tottalPrice + this.shippingCost
		},
	},
	methods: {
		handleSearchingProvider() {
			this.providerID = ''
		},
		handleSelectProvider(e) {
			this.providerID = e.id
		},
		handleCreateNewProvider(e) {
			this.providerID = e.providerID
			this.$refs.inputSearchProvider.providerName = e.providerName
			this.$refs.inputSearchProvider.providerID = e.providerID
		},
		openModalCreateProvider() {
			this.$refs.modalAddProvider.openModal()
		},
		openModalAddStockIn() {
			this.$refs.modalAddStockIn.openModal()
		},
		addStockIn({ goodsID, expiryDate, costPrice, quantity }) {
			if (!this.stockIn[goodsID]) this.stockIn[goodsID] = {}
			if (!this.stockIn[goodsID][expiryDate]) this.stockIn[goodsID][expiryDate] = {}
			this.stockIn[goodsID][expiryDate] = {
				quantity: (this.stockIn[goodsID][expiryDate].quantity || 0) + quantity,
				costPrice,
			}
		},
		editStockIn(goodsID, expiryDate) {
			this.$refs.modalAddStockIn.openModal({
				goodsID,
				goodsName: this.goodsList[goodsID].goodsName,
				group: this.goodsList[goodsID].group,
				expiryDate: Number(expiryDate),
				quantity: this.stockIn[goodsID][expiryDate].quantity,
				costPrice: this.stockIn[goodsID][expiryDate].costPrice,
				retailPrice: this.goodsList[goodsID].retailPrice,
				wholesalePrice: this.goodsList[goodsID].wholesalePrice,
			})
			delete this.stockIn[goodsID][expiryDate]
		},
		handleCancleForm() {
			this.$router.back()
		},
		notify(message) {
			this.$notification.open({
				message: 'Error !!!',
				description: message,
				placement: 'topRight',
				duration: 2,
			})
		},
		checkValidation() {
			if (!this.providerID) {
				this.notify('You are required to choose a specific provider !')
				return false
			}
			if (Object.keys(this.stockIn).length === 0) {
				this.notify('You must choose at least one goods !')
				return false
			}
			return true
		},
		async startCreateNote() {
			if (!this.checkValidation()) return
			this.loadingCreateNote = true
			const newNote = {
				providerID: this.providerID,
				shippingCost: this.shippingCost,
				stockIn: this.stockIn,
				totalMoney: this.totalMoney,
			}
			await addImportNote(newNote)
			this.loadingCreateNote = false
			this.$router.back()
		},
		formatDateTime(time) {
			return MyFormatDateTime(time, 'DD/MM/YY')
		},
	},
}
</script>

<style lang="scss" scoped>
.form {
	max-width: 800px;
	margin: 0 auto;
}
.wrapper-table {
	width: 100%;
	overflow-x: scroll;
	margin-bottom: 0.5rem;
}
</style>
