import {
	getFirestore,
	collection,
	doc,
	query,
	where,
	orderBy,
	limit,
	onSnapshot,
	runTransaction,
	arrayUnion,
} from 'firebase/firestore'
import { reactive } from 'vue'

const nowTime = new Date().getTime()
const importNoteList = reactive({})
const db = getFirestore()
const qr = query(
	collection(db, 'IMPORTNOTE'),
	where('updatedAt', '>', nowTime - 7 * 24 * 60 * 60 * 1000),
	orderBy('updatedAt', 'asc'),
	limit(10),
)

onSnapshot(qr, snapshot => {
	snapshot.docChanges().forEach(change => {
		if (change.type === 'added' || change.type === 'modified') {
			importNoteList[change.doc.id] = change.doc.data()
		}
		if (change.type === 'removed') {
			delete importNoteList[change.doc.id]
		}
	})
})

const addImportNote = async noteData => {
	const importNoteRef = doc(collection(db, 'IMPORTNOTE'))

	await runTransaction(db, async transaction => {
		const queryGoodsRefList = Object.keys(noteData.stockIn).map(goodsID =>
			transaction.get(doc(db, 'WAREHOUSE', goodsID)),
		)
		const goodsDocList = await Promise.all(queryGoodsRefList)
		goodsDocList.forEach(goodsDoc => {
			const { stockAvail } = goodsDoc.data() || {}
			Object.entries(noteData.stockIn[goodsDoc.id]).forEach(([expiryDate, { quantity, costPrice }]) => {
				if (!stockAvail[expiryDate]) stockAvail[expiryDate] = {}
				stockAvail[expiryDate] = {
					costPrice,
					quantity: (stockAvail[expiryDate].quantity || 0) + quantity,
				}
			})
			transaction.update(doc(db, 'WAREHOUSE', goodsDoc.id), {
				stockAvail,
				importNoteIDList: arrayUnion(importNoteRef.id),
			})
		})

		transaction.set(importNoteRef, {
			providerID: noteData.providerID,
			shippingCost: noteData.shippingCost,
			totalMoney: noteData.totalMoney,
			stockIn: noteData.stockIn,
			createdAt: new Date().getTime(),
			updatedAt: new Date().getTime(),
			removedAt: 0,
		})

		transaction.update(doc(db, 'PROVIDER', noteData.providerID), {
			importNoteIDList: arrayUnion(importNoteRef.id),
		})
	})
}

export { importNoteList, addImportNote }
