<template>
	<div class="input-date-time">
		<a-input :value="refDay" @update:value="onInputDay" class="input-day" type="number" />
		<span class="dash">-</span>
		<a-input :value="refMonth" @update:value="onInputMonth" class="input-month" type="number" />
		<span class="dash">-</span>
		<a-input :value="refYear" @update:value="onInputYear" class="input-year" type="number" />
	</div>
</template>

<script>
import { ref, watch } from 'vue'

export default {
	props: ['datetime'],
	setup(props) {
		const refDay = ref('')
		const refMonth = ref('')
		const refYear = ref('')
		watch(
			() => props.datetime,
			dt => {
				if (dt === '-') return
				const propTime = new Date(dt)
				if (propTime.toString === 'Invalid Date') {
					refDay.value = ''
					refMonth.value = ''
					refYear.value = ''
				} else {
					refDay.value = `0${propTime.getDate()}`.slice(-2)
					refMonth.value = `0${propTime.getMonth() + 1}`.slice(-2)
					refYear.value = propTime.getFullYear()
				}
			},
		)
		return { refDay, refMonth, refYear }
	},
	methods: {
		onInputDay(e) {
			this.refDay = Number(e) > 31 ? e.slice(-1) : e
			this.emitParent()
		},
		onInputMonth(e) {
			this.refMonth = Number(e) > 12 ? e.slice(-1) : e
			this.emitParent()
		},
		onInputYear(e) {
			this.refYear = `20${e}`.slice(-4)
			this.emitParent()
		},
		emitParent() {
			if (!this.refDay || !this.refMonth || !this.refYear || this.refYear.length < 4) {
				this.$emit('update:datetime', '-')
			} else {
				const getDate = new Date(`${this.refYear}-${this.refMonth}-${this.refDay}`)
				this.$emit('update:datetime', getDate.getTime() || '-')
			}
		},
	},
}
</script>

<style lang="scss" scoped>
/* Chrome, Safari, Edge, Opera : Remove arrows/spinner input number */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
/* Firefox Remove arrows/spinner input number */
input[type='number'] {
	-moz-appearance: textfield;
}

input {
	border: none;
	outline: none;
}

.input-date-time {
	display: flex;
	width: 160px;
	border: 1px solid #d9d9d9;
}
.input-day {
	width: 40px;
}
.input-month {
	width: 40px;
}
.input-year {
	width: 60px;
}
.dash {
	font-size: 18px;
}
</style>
